<template>
    <div id="content_wrap">
        <Header2 breadcrumbs="작품 정보"> <!--왼쪽이 받는애(breadcrumbs) 오른쪽이 값을 주는 에 (작품정보) -->
        </Header2>
        <div id="work_detail_info">
            <div class="topWork_info container">
                <div class="col-md-3 item_thumbnail">
                    <div class="thumbnail" v-bind:style="{ 'background-image': 'url(' + fanficInfo.THUMBNAIL_URL + ')' }">
                    </div>
                </div>
                <div class="col-md-9 topWork_info_sub row">
                    <div class="topWork_title">
                        {{fanficInfo.TITLE}}
                    </div>
                    <div class="topWork_sub_title">
                        <span class="item_telent">
                            {{fanficInfo.TARGET_STAR.DATAS[0].STAR_NAME}}
                        </span>
                        <span class="item_complete" v-if="fanficInfo.IS_FINISHED=='Y'">
                            완결
                        </span>
                        <span class="item_complete" v-else>
                        </span>
                    </div>
                    <div class="topWork_sub_info">
                        <span class="topWork_episode_info">
                            에피소드
                            <span v-if="fanficInfo.TOTAL_EPISODE_CNT > '0'">{{ fanficInfo.TOTAL_EPISODE_CNT }}개</span>
                            <span style="color: red;" v-else="fanficInfo.TOTAL_EPISODE_CNT=='0'">0개</span>
                        </span>
                        <span class="topWork_episode_info_sub">
                            <img src="../assets/ic-person.svg" class="info_views">
                            <span class="viewTxt">{{ fanficInfo.VIEW_CNT }}</span>
                            <img src="../assets/ic-star.svg" class="info_star">
                            <span class="scoreTxt">{{ fanficInfo.SCORE }}</span>
                        </span>

                    </div>
                    <router-link :to="{ name: 'WorkModify', params: { id: fanficInfo.FANFIC_IDX }} ">
                        <button class="topWork_info_edit">
                            작품정보 수정
                        </button>
                    </router-link >
                </div>
            </div>
            <div class="workIntro">
                <div class="workIntro_title">
                    작품 소개
                </div>
                <div class="workIntro_detail">
                    {{ fanficInfo.DESCRIPTION }}
                </div>
            </div>
        </div>
        <div class="listView">
            <span class="totalNumber">전체({{ fanficInfo.TOTAL_EPISODE_CNT }})</span>
            <button class="listOrdering" id="latest" v-on:click="sortOutLatest">최신화부터</button>
            <button class="listOrdering2" id="old" v-on:click="sortOutOld">첫화부터</button>
        </div>
        <div class="btn_wrapper">
            <button type="button" class="btn_episode_add" data-toggle="modal" data-target="#myModal">
                에피소드 추가
            </button>
            <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
              <div class="modal-dialog">
                <div class="modal-content">
                  <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true"><img src="../assets/ic-clear.svg" /></span>
                    </button>
                    <div class="modal-title" id="myModalLabel">{{fanficInfo.TITLE}}</div>
                  </div>
                  <div class="modal-body">
                      <div class="form-group">
                          <label>
                              에피소드 제목
                          </label>
                          <span class="charactersInfo"></span>
                          <div class="formControlBox">
                              <input class="formControl yourtextarea" maxlength="30" type="text" required placeholder="제목을 입력해 주세요" v-model="title">
                          </div>
                          <div class="validateAlert" v-if="title.length == 0">
                              <span>내용을 입력해 주세요</span>
                          </div>
                      </div>
                  </div>
                  <div class="modal-footer">
                    <button type="button" class="btn-primary-red" v-on:click="registerTitle">
                        다음
                    </button>
                  </div>
                </div>
              </div>
            </div>
        </div>

        <div class="episodeList" v-for="episode in episodeList">
            <div class="episdoeItem container">
                <div class="itemNumber col-md-1" v-if="episode.IS_PUBLIC=='N'">

                    {{convertNumber(parseInt(episode.ORDER_NUMBER)+1,2)}}
                </div>
                <div class="itemNumber col-md-1" v-else-if="episode.IS_PUBLISHED=='N'">
                    {{episode.ORDER_NUMBER}}
                </div>
                <div class="itemNumber2 col-md-1" v-else>
                    {{episode.ORDER_NUMBER}}
                </div>
                <div class="item_info col-md-10">
                    <div class="item_title">
                            {{episode.TITLE}}
                        <span class="item_lock" v-if="episode.IS_PUBLIC=='Y'">
                            <img src="../assets/ic-lock.svg" class="ic_lock">
                        </span>
                        <button class="item_save" type="button" disabled v-if="episode.IS_PUBLISHED=='N'">
                            임시저장
                        </button>
                    </div>
                    <div class="item_rate">
                        <img src="../assets/ic-person.svg" class="info_views">{{episode.VIEW_CNT}}
                        <img src="../assets/ic-star.svg" class="info_star">{{episode.SCORE}}
                        <span class="item_date">{{episode.INS_DATE}}</span>
                    </div>
                </div>
                <div class="btn_more col-md-1 dropdown">
                    <button id="dLabel" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <img src="../assets/ic-more-horiz.svg" class="btn_more_img dropbtn">
                    </button>
                      <ul class="dropdown-menu" role="menu" aria-labelledby="dLabel">
                          <li class="dropbtnList">
                              <a href="#">에피소드 제목 수정</a>
                          </li>
                          <li class="dropbtnList">
                              <a href="#">에피소드 내용 수정</a>
                          </li>
                          <li class="dropbtnList">
                              <a href="#">에피소드 공개 설정</a>
                          </li>
                          <li class="dropbtnList">
                              <a href="#">에피소드 삭제</a>
                          </li>
                      </ul>
                </div>
            </div>
        </div>

    </div>

</template>

<script type="text/javascript">
import Header2 from '@/components/Header';
export default {
  name: 'WorkDetail',
  components: {
      Header2
  },
  beforeCreate() {
  },
  created() { //화면생성할때 쓰임
      this.getContentList();
  },
  beforeMount() {

  },
  mounted() {
      $(document).on('keyup','.modal',function(){
          $(".yourtextarea").keyup(function(){
              $(".charactersInfo").text(($(this).val().length)+"/30");
          });
      });
  },
  methods: {

      convertNumber: function (n, width) {
          n = n + '';
          return n.length >= width ? n : new Array(width - n.length + 1).join('0') + n;
      },
      plusNum:function (n, width) {
          var n = this.episodeList;
          var zero = '';
          n = n.toString();

          if (n.length < digits) {
            for (var i = 0; i < digits - n.length; i++)
              zero += '0';
          }
          return zero + n;
      },
      sortOutLatest: function(){
         this.episodeList.sort(function(a,b){
            return b.ORDER_NUMBER - a.ORDER_NUMBER;
        });
        $("#latest").hide();
        $("#old").show();
      },
      sortOutOld: function(){
          this.episodeList.sort(function(a,b){
              return a.ORDER_NUMBER - b.ORDER_NUMBER;
          });
          $("#latest").show();
          $("#old").hide();
        },
      getContentList() {
          const baseURI = '/api/v8/GetFanficInfo';
          var userId = '9f1129ef534560b29b9e0ab1b5e27fa1595fba9b00db3e094391bea6255cc6cc';
          var fanficIdx = this.$route.params.id;

          this.$http.get(`${baseURI}/${userId}/${fanficIdx}`)//이거 할거야. (어디로=url)
            .then((result) => { //그러면, 성공했을때 ~해라.
              console.log(result)
              this.fanficInfo = result.data
          })

        const baseURI2 = '/api/v8/GetFanficEpisodeList'; //사실 위에 선언한게 이미 호출되었기 때문에 따로 선언을 다시 해줄 필요는 없다. (새로 담아져도 괜찬)

        this.$http.get(`${baseURI2}/${userId}/${fanficIdx}`)//이거 할거야. (어디로=url). 필요한 데이터가 두개이므로 두번 써줌.
          .then((result) => { //그러면, 성공했을때 ~해라. 여기서 (result)는 매개변수. function(result){}를 줄인형태임.
            console.log(result)
            this.episodeList = result.data.DATAS // vue.this~ 에 매개변수result의 데이터를 담아줌. 그럼 vue가 그 데이터로 그려줌. result.data.필요한데이터키값
          })
      },
      // registerTitle: 제목을 가지고 WorkEpisode 페이지로 넘어간다
      registerTitle: function(){
          $('#myModal').modal('hide');

          // = data(){}의 title을 가져온것
          // 생성될 episode의 제목
          var episodeTitle = this.title;

          // 에피소드 저장시에 필요한 fanfixIdx 정보를 넘겨준다
          var fanficIdx = this.$route.params.id;

          this.$router.push(`/work/episode/${fanficIdx}/${episodeTitle}`)//해당 경로로 이동하게 해준다.
      }

    },

  data() {
    return {
      fanficInfo: [],
      episodeList: [],
      title:"",
    };
  },
};
</script>

