<template>
    <div id="subheader" role="navigation">
        <div class="header_wrap">
            <ul class="nav_left">
                <router-link to="/work">
                    <li class="back">
                         <img src="../assets/ic-back.svg" class="backBtn">
                    </li>
                </router-link>
                <li class="nav-title">
                    {{ title }}
                </li>
                <li class="saveMessage">
                    임시 저장 중입니다.
                </li>
            </ul>
            <ul class="nav_right">
                    <a class="navText" href="">
                        모바일 미리보기
                    </a>
                <li>
                    <button class="workSave">
                        저장
                    </button>
                </li>
                <li>
                    <button class="work_upload">
                        출판
                    </button>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
  name: 'SubHeader',
  created() { //화면생성할때 가져와야하는 데이터. 또는 실행되는 애들
      this.getTitle();
  },
  data() {
    return {
        title:"",
    };
  },
  methods:{
      getTitle(){
          // this를 쓸때 :
          // 1번 data(){}에 있는 변수. this.title
          // 2번 메소즈에 있는 함수호출할때. this.getTitle();
          // 3번 뷰의 뭔가에 접근할때 this.$route
          this.title = '에피소드 : ' + this.$route.params.title ;
      }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

    #subheader{
        position: relative;
        height: 87px;
        border-bottom: solid 1px #dee2e6;
        background-color: #fff;
        padding: 24px 0px 24px 0px;
        margin: 0;
    }
    .nav-title{
        font-size: 18px;
        font-weight: bold;
        text-align: left;
        color: rgba(0, 0, 0, 0.87);
        float: left;
        margin: auto;
        margin-left: 16px;
    }
    .saveMessage{
        text-align: left;
        color: #868e96;
        margin: auto;
        margin-left: 16px;
    }
    .navText{
        font-size: 14px;
	    text-align: center;
        color: rgba(0, 0, 0, 0.87);
        margin: auto;
        margin-right: 32px;

    }
    .workSave{
        border-radius: 18px;
        border: solid 1px #ff5656;
        background-color: #fff;
        color: #ff5656;
        padding: 8px 32px;
        display: inline-block;
        font-size: 14px;
        font-weight: lighter;
        margin-right: 16px;
    }
    .work_upload{
        border-radius: 18px;
        background-color: #ff5656;
        color: #fff;
        padding: 8px 32px;
        display: inline-block;
        font-size: 14px;
        font-weight: lighter;
    }
    li{
        list-style: none;
    }
    button{
        border-style: none;
    }
    .nav_left{
        float: left;
        display: flex;
        padding: 0px;
        margin-left: 32px;

            }
    .nav_right{
        float: right;
        display: flex;
        margin-right: 32px;
            }
</style>



