                    $('#freeImageListModal').modal('hide');
                    $('#freeImageListSubModalBg').modal('hide');
                    $('#imageListResultModal').modal();

                    this.$refs.freeImageResultModalPagination.setPage(1);
        },
        sendCropModal(freeImageBgSub){
            // if(this.selectScriptOrSceneConfirm == "1"){
            //     this.sceneForSelectedGallery.scene_image = freeImageBgSub.URL;
            // }
            // else if (this.selectScriptOrSceneConfirm == "2") {
            //     this.scriptForSelectedGallery.image = freeImageBgSub.URL;
            // }
            // else{
            //     this.characterAddObject.upload_photo = freeImageBgSub.URL;
            // };
            $('#imageListResultModal').modal('hide');
            $('#starResultModal').modal('hide');
            $('#cropModal').modal();
            // var image = document.getElementById('cropImage');
            this.selectCropImage = freeImageBgSub.URL;
            this.$refs.cropper.replace(freeImageBgSub.URL);

            // var cropper = new Cropper(image, {
            //     aspectRatio: 16 / 9,
            //     viewMode: 1,
            //     crop: function(e) {
            //         console.log(e.detail.x);
            //         console.log(e.detail.y);
            //         console.log(e.detail.width);
            //         console.log(e.detail.height);
            //         console.log(e.detail.rotate);
            //         console.log(e.detail.scaleX);
            //         console.log(e.detail.scaleY);
            //     }
            // });
        },
        addImageForSelectObject(cropImg, cropImgBlob){
            if(this.selectScriptOrSceneConfirm == "1"){
                this.sceneForSelectedGallery.scene_image = cropImg;
                this.sceneForSelectedGallery.image_blob = cropImgBlob;
            }
            else if (this.selectScriptOrSceneConfirm == "2") {
                this.scriptForSelectedGallery.image = cropImg;
                this.scriptForSelectedGallery.image_blob = cropImgBlob;
            }
            else{
                this.characterAddObject.upload_photo = cropImg; //화면에 띄어주는 용도 (=url)
                this.characterAddObject.image_blob = cropImgBlob;//서버에 보내는 용도. 서버에 보낼 때 얘로 보내야함(파일)
            };
        },
    },
};



</script>


