<template>
    <div id="content_wrap">
        <Header2 breadcrumbs="작품 정보 등록">
        </Header2>
        <div class="notiMessage">
            작품 정보 등록 후 에피소드 추가가 가능해요!
        </div>

        <div class="formBox">
            <div class="photoColumn col-md-6">
                <div class="subTitle">
                    작품 표지 이미지
                </div>
                <div class="photo file-upload">
                      <div v-if="!image">
                          <label for="ex_file">
                              <img src="../assets/ic-add.svg" class="ic-add">
                          </label>
                        <input type="file" @change="onFileChange" class="upload">
                      </div>
                      <div v-else>
                        <img :src="image" class="coverImg"/>
                        <button @click="removeImage" class="removeBtn">
                            <img src="../assets/ic-clear.svg" class="icClear">
                        </button>
                      </div>
                </div>
            </div>
            <div class="select-group col-md-6">
                <div class="form-group">
                    <label>
                        언어선택
                    </label>
                    <select v-model="lang_type">
                        <option value="ko_KR">
                            한국어
                        </option>
                        <option value="en_US">
                            English
                        </option>
                        <option value="zh_CN">
                            中文(漢語)
                        </option>
                        <option value="ja_JP">
                            日本語
                        </option>
                    </select>
                </div>
                <div class="form-group">
                    <label>
                        연예인 선택
                    </label>
                    <select v-model="choose_star">
                        <option v-for="star in starList" v-bind:value="star.STAR_IDX">
                            <!--선택한 값이 choose star에 담김. 반대로 vue.js에서 값을 수정할 경우 html에 보여지는 값이 바뀜. 양방향 데이터바인딩.
                            v-bind value는 각 옵션에 실제 value를 부여해줌. 옵션을 선택했을 떄 값이 담기는건 choose_star임 -->
                            {{ star.STAR_NAME }}
                            <!--보여주기 위한 값 -->
                        </option>
                    </select>
                </div>
                <div class="form-group">
                    <label>
                        작품 제목
                    </label>
                    <span class="charactersInfo"></span>
                    <div class="formControlBox">
                        <input v-model="title" class="formControl yourtextarea" maxlength="30" name="workTitle" type="text" required placeholder="제목을 입력해 주세요">
                        <div class="validateAlert" v-if="title.length == 0">
                            <span>내용을 입력해 주세요</span>
                        </div>
                    </div>
                </div>
                <div class="form-group">
                    <label>
                        작품 소개
                    </label>
                    <span class="charactersInfo2"></span>
                    <div class="formControlBox">
                        <textarea v-model="description" class="formControl introText yourtextarea2"maxlength="300" name="workTitle" type="text" required placeholder="어떤 작품인지 소개해 주세요">
                        </textarea>
                        <div class="validateAlert" v-if="description.length == 0">
                            <span>내용을 입력해 주세요</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="btnCenter">
            <button class="btn_publish" id="submit" v-on:click="submit">
                작품 등록
            </button>
        </div>
    </div>
</template>

<script type="text/javascript">

import Header2 from '@/components/Header';
import axios from 'axios';

export default {
    name: 'WorkUpload',
    components: { Header2 },
    beforeCreate() {
    },
    created() { //화면생성할때 쓰임
        this.getStarList();
    },
    beforeMount() {
    },

    mounted() {
        $(".yourtextarea").keyup(function(){
            $(".charactersInfo").text(($(this).val().length)+"/30");
        });
        $(".yourtextarea2").keyup(function(){
            $(".charactersInfo2").text(($(this).val().length)+"/300");
        });
    },
    data() {
        return{
            identity:'9f1129ef534560b29b9e0ab1b5e27fa1595fba9b00db3e094391bea6255cc6cc',
            title:'',
            description: '',
            choose_star: null, // 연예인의 목록 중 선택한 값을 보내주기 위한 것.
            lang_type: "ko_KR",
            image: null,
            imageFile: null,
            starList: [], // 연예인의 목록을 보여주기 위한 것.
            model: {//데이터 속성에 따른 분류. 사용자가 직접 입력한 데이터만 따로 분류.
                upload_phone: null,
                identity: null,
                title: null,
                description: null,
                lang_type: null,
                choose_stars: []
            }
        };
    },
    methods: {
        onFileChange(e) {
            var files = e.target.files || e.dataTransfer.files; // || -> or. 내가 업로드한 파일이 여기 혹은 저기 있음. 인풋에서 업로드한 파일에 대한 조건인거임.
            if (!files.length) {
                return;
            }
            this.createImage(files[0]);//브라우져에 띄울 이미지 만드는용. files[0]=내가 업로드한 파일
            this.imageFile = files[0]//실제로 서버세 전송하기 위한 용도의 파일을 이미지파일에 저장.
        },
        createImage(file) { //이미지파일을 이미지로 변환해서 브라우져에 띄어준다
            var image = new Image();
            var reader = new FileReader();
            var vm = this;

            reader.onload = (e) => {
                vm.image = e.target.result;
            };
            reader.readAsDataURL(file);
        },
        removeImage: function (e) {
            this.image = null;
        },
        submit: function(){
            // Object.keys()메소드 사용해서 반복수행하도록 개선하기
            // Objecy.keys()는 입력받은 argument:Object의 key를 배열로 반환한다.
            const formData = new FormData()
            formData.append('upload_photo', this.imageFile);
            formData.append('identity', this.identity);
            formData.append('title', this.title);
            formData.append('description', this.description);
            formData.append('lang_type', this.lang_type);
            formData.append('choose_star[]', this.choose_star)

            //

            axios({
                method: 'POST',
                url: '/api/v8/AddFanfic',
                data: formData,//=body
                headers: {
                    'Content-Type': 'multipart/form-data'//body가 아래와 같은 포멧으로 되어 있다는걸 알려주는 것.
                },
            },
            ).then((response) => { //response에 서버에서 내려준 값을 받음. = function(response) {}
                    console.log(response);
                    alert('작품이 등록되었습니다.');
                    var fanfic_idx = response.data.message;
                    this.$router.push(`/work/detail/${fanfic_idx}`)
                }
            )
        },
        getStarList() {
            const baseURI2 = 'api/v2/GetSubscriptionList'; //사실 위에 선언한게 이미 호출되었기 때문에 따로 선언을 다시 해줄 필요는 없다. (새로 담아져도 괜찬)
            var lang = "ko";
            var userId = '9f1129ef534560b29b9e0ab1b5e27fa1595fba9b00db3e094391bea6255cc6cc';
            var packageName = "com.photocard.master";
            this.$http.get(`${baseURI2}/${lang}/${userId}/${packageName}`)//이거 할거야. (어디로=url). 필요한 데이터가 두개이므로 두번 써줌.
            .then((result) => { //그러면, 성공했을때 ~해라. 여기서 (result)는 매개변수. function(result){}를 줄인형태임.
                console.log(result)
                this.starList = result.data // vue.this~ 에 매개변수result의 데이터를 담아줌. 그럼 vue가 그 데이터로 그려줌. result.data.필요한데이터키값
                this.choose_star = result.data[0].STAR_IDX
                //0이라는 인덱스로 호출했을때 나오는 결과값이 객체이기 때문에 value에 할당된 숫자와는 다른 형식의 데이터. 때문에 일치하지 않음. STAR_IDX의 결과값은 숫자이기 때문에, v-bind:value에 할당된 값과 일치함.
            })
        }
    },
};



</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.validateAlert{
    text-align: right;
    margin-top: 4px;
    font-size: 14px;
    color: #ff5656;
}
.icClear{
    width: 16px;
    height: 16px;
}
.removeBtn{
    width: 24px;
	height: 24px;
	background-color: #ffffff;
    border-radius: 50%;
    border: solid 1px #ced4da;
    position: absolute;
    right: 38px;
    top: 44px;
    text-align: center;
    padding: 0px;
}
input.upload {
  opacity: 0;
  position: relative;
  width: 48px;
  height: 48px;
  margin: 0;
  position: absolute;
  top: 55%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
#content_wrap{
    width: 1000px;
    margin: auto;
}
.introText{
    height: 221px;
    -webkit-appearance: none;
}
.charactersInfo{
    float: right;
    color: #ced4da;
}
.charactersInfo2{
    float: right;
    color: #ced4da;
}

.ic-add{
    width: 48px;
	height: 48px;
    margin: 0;
    position: absolute;
    top: 55%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}
.notiMessage{
    font-size: 24px;
    text-align: center;
    color: #343a40;
    margin-top: 24px;
    margin-bottom: 40px;
}
.form-group{
    margin: 0px;
}
label{
    font-size: 16px;
    text-align: left;
	color: rgba(0, 0, 0, 0.87);
    font-weight: normal;
    margin-bottom: 8px;
}
.form-group select{
    background-image: url("../assets/ic_expand_more.svg");
    background-repeat: no-repeat;
    background-position: right 16px center;
    background-size: 12px;
    -webkit-appearance: none;
    border-radius: 5;
    line-height: 2;
    padding: 12px 16px;
    border: 1px solid #ced4da;
    width: 100%;
    background-color: #fff;
    font-size: 16px;
    margin-bottom: 24px;
    color: #343a40;
}
.formBox{
    width: 740px;
    margin: auto;
}
.photo{
    width: 328px;
	height: 224px;
	border-radius: 5px;
	border: solid 1px #ced4da;
}
.coverImg{
  width: 328px;
  height: 224px;
  margin: auto;
}
.subTitle{
    font-size: 16px;
    margin-bottom: 8px;
    color: #343a40;
}
.formControlBox{
    margin-bottom: 24px;
}
.formControl{
    border-radius: 5px;
    padding: 16px;
    border: 1px solid #ced4da;
    width: 100%;
    background-color: #fff;
    font-size: 16px;
    color: #343a40;
}
::-webkit-input-placeholder {
  color: #ced4da;
}
.btn_publish{
    padding: 16px 60px;
    font-size: 16px;
	text-align: center;
	color: #ffffff;
    border-radius: 33.5px;
	background-color: #ff5656;
	box-shadow: 0 3px 6px 0 rgba(255, 86, 86, 0.63);
    font-weight: lighter;
    border-style: none;
    margin-top: 16px;
}
.btnCenter{
    text-align: center;
}
</style>



