<template>
    <div id="FanficEditor">
        <div class="FanficEditor_wrap container">
            <div class="sceneList"
            v-for="(scene, index) in sceneList"
            :scene="scene"
            @onClickSceneAddButton="addScene(index)">
                <div class="sceneTitle">
                    장면{{index}}
                </div>
                <div class="bgArea">
                    <div class="bgTitle">
                        배경이미지
                    </div>
                    <div class="bg">
                    </div>
                    <span class="bgError">배경 이미지를 선택해 주세요.</span>
                </div>
            </div>
        </div>
        <div class="container">
            <episodeScript
            v-for="(script, index) in scriptList"
            :script="script"
            @onClickAddButton="addScript(index)"
            @onClickRemoveButton="removeScript(index)"
            >
            <!--스크립트리스트의 갯수만큼 반복문을 도는데 그 반복되는 요소의 이름이 script인것.스크립트의 갯수만큼 이 에피소드 스크립트를 반복해라 -->
            </episodeScript>
        </div>
    </div>
</template>



<script>
import episodeScript from '@/components/episodeScript';
export default {
    name: 'FanficEditor',
    components: { episodeScript },
    data() {
        return {
            scriptList:[], //얘가 원본이라고
            sceneList: [],
        };
    },
    methods:{
        addScene(index){
            let scene = {
                "scen_position": 0,
                "scen_image": "",
            }
            this.sceneList.push(scene);
        },
        addScript(index) {
            let script = {
                "time": "",
                "loading": false,
                "star": "",
                "star_image": "",
                "star_seq": "",
                "image": "",
                "content": ""
            };
            this.scriptList.splice(index+1,0,script);
        },
        removeScript(index) {
            this.scriptList.splice(index, 1);
        },
    },
    created() { //화면생성할때 쓰임
        this.addScript();
        this.addScene();
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
    width: 100%;
    padding: 0px;
}
.row{
    margin: 0px;
}
.sceneTitle{
    font-size: 32px;
    font-weight: bold;
    text-align: left;
    color: #343a40;
    margin-bottom: 16px;
}
.bgTitle{
    font-size: 14px;
    color: #343a40;
    margin-bottom: 8px;
}
.bg{
    width: 100px;
    height: 100px;
    border-radius: 5px;
    border: solid 1px #dee2e6;
    display: inline-block;
}
.bgError{
    margin-left: 8px;
    font-size: 14px;
    color: #ff5656;
}
.bgArea{
    margin-bottom: 24px;
}
</style>



