<template>
    <div id="SideList" role="navigation">
        <div class="aside_wrap">
            <ul class="list">
                <li v-for="(scene, index) in sceneList">
                    <div class="aTable" :class="index == selectedIndex ? 'active' : ''"  @click="onClickSelectedScene(index)">
                        장면{{ index+1 }}
                        <button class="aTable-remove" :class="index == selectedIndex ? 'active' : ''" @click="onClickRemoveSceneButton(index)">
                            <img src="../assets/ic-clear.svg" class="removeBtn">
                        </button>
                    </div>
                </li>
                <li>
                    <button class="sceneAdd" @click="onClickSceneAddButton">장면 추가</button>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
  name: 'SideList',
  props: {
      sceneList: {
          type: Array,
          //옵셔널. 스크립트를 받을건데 이게 넘어온 값을 오브젝트 타입으로 받겠다. 만약 오브젝트가 아니면 에러남.
      }
  },
  data() {
    return {
        selectedIndex:"-1"
    };
  },
  methods : {
      onClickSceneAddButton() {
          this.$emit('onClickSceneAddButton');
      },
      onClickSelectedScene(index){
          this.selectedIndex = index
      },
      onClickRemoveSceneButton(index){
          this.$emit('onClickRemoveSceneButton', index);
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sceneAdd{
    font-size: 16px;
    text-align: center;
	color: #343a40;
    background-color: #e9ecef;
    border-radius: 5px;
    padding: 9px;
    width: 162px;
    margin-top: 32px;
    border-style: none;
}
#SideList{
    max-width: 162px;
    min-width: 162px;
}
ul{
    padding: 0px;
}
li{
    list-style: none;
}

.aTable{
    font-size: 16px;
    text-align: left;
	color: #343a40;
    padding: 9px;
    width: 162px;
    background-color: inherit;
    border-style: none;
    border-radius: 5px;

}

.aTable-remove {
    display: none;
}

.aTable.active{
    background-color: #e9ecef;
}

.aTable-remove.active {
    display: block;
    float: right;
    border-style: none;
    background-color: inherit;
    padding: 0px;
    margin: auto;
}
.removeBtn{
    width: 20px;
    height: 20px;
}
</style>



