<template>
    <div id="content_wrap">
        <Header2>
        </Header2>

        <div v-if="contentList.length > 0">
            <div id="work_list">
                <div class="work_list_item container" v-for="content in contentList">
                    <div class="col-md-2 item_thumbnail">
                        <div class="thumbnail" v-bind:style="{ 'background-image': 'url(' + content.THUMBNAIL_URL + ')' }">
                        </div>
                    </div>
                    <div class="col-md-8 item_info">
                        <router-link :to="{ name: 'workDetail', params: { id: content.FANFIC_IDX }}">
                            <div class="item_title">
                                {{ content.TITLE }}
                            </div>
                        </router-link>
                        <div class="item_sub_title">
                            <span class="item_telent">
                                {{ content.WRITER_NICKNAME }}
                            </span>
                            <span class="item_complete" v-if="content.IS_FINISHED=='Y'">
                                완결
                            </span>
                            <span class="item_complete" v-else>
                            </span>
                        </div>
                        <div class="item_episode">
                            에피소드
                            <span v-if="content.TOTAL_EPISODE_CNT > '0'">{{ content.TOTAL_EPISODE_CNT }}개</span>
                            <span style="color: red;" v-else="content.TOTAL_EPISODE_CNT=='0'">0개</span>
                        </div>
                        <div class="item_rate">
                            <span class="info_viewsArea">
                                <img src="../assets/ic-person.svg" class="info_views">{{ content.VIEW_CNT }}
                            </span>
                            <span class="info_starArea">
                                <img src="../assets/ic-star.svg" class="info_star">{{ content.SCORE }}
                            </span>
                        </div>
                    </div>
                    <div class="col-md-2 btn_more dropdown">
                        <div class="btn_item_more">
                            <button id="dLabel" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <img src="../assets/ic-more-horiz.svg" class="btn_more_img dropbtn">
                            </button>
                            <ul class="dropdown-menu" role="menu" aria-labelledby="dLabel">
                                <li class="dropbtnList">
                                    <a href="#">작품정보 수정</a>
                                </li>
                                <li class="dropbtnList">
                                    <a href="#">작품 삭제</a>
                                </li>
                            </ul>
                        </div>
                        <router-link to="/work/episode">
                            <span v-if="content.TOTAL_EPISODE_CNT=='0'">
                                <button class="btn_episode_add">
                                    에피소드 추가
                                </button>
                            </span>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <div v-else>
            <div class="emptyArea">
                <img src="../assets/img_empty_myhome.svg" class="emptyIllustHome">
                <div class="emptyText">
                    아직 작품이 없습니다.
                    첫 작품을 등록 해 보세요.
                </div>
                <router-link to="/work/upload">
                    <button class="btnRedshadow">
                        작품 등록하기
                    </button>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>

import Header2 from '@/components/Header';
import apigClientFactory from 'aws-api-gateway-client';
import axios from 'axios';

export default {
    name: 'Home',
    components: { Header2 },
    methods: {

        getContentList() {

            const baseURI = '/api/v8/GetMyFanficList';
            let userId = '9f1129ef534560b29b9e0ab1b5e27fa1595fba9b00db3e094391bea6255cc6cc';

            this.$http.get(`${baseURI}/${userId}`)//이거 할거야. (어디로=url)
            .then(res => { //그러면, 성공했을때 ~해라.
                this.contentList = res.data.FANFIC_LIST
            }, err => {
                console.log(err);
            });
        }
    },
    data() {
        return {
            msg: 'Welcome to Your Vue.js App',
            contentList: [], //vue.js는 순서와 상관없이 코드가 실행
        };
    },
    created() {
        this.getContentList();
    },
};

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#content_wrap{
    width: 1000px;
    margin: auto;
}
button{
    border-style: none;
}
.item_sub_title{
    margin-top: 6px;
}
.item_title{
    font-size: 20px;
    text-align: left;
    color: #343a40;
}
.item_thumbnail{
    padding: 0px;
}
.thumbnail{
    width: 175.4px;
    height: 120px;
    background-color: #000;
    border-radius: 5px;
}
.item_telent{
    font-size: 14px;
    text-align: left;
    color: #343a40;
}
.item_complete{
    font-size: 14px;
    text-align: left;
    color: #ff5656;
    margin-left: 8px;
}
.item_episode{
    font-size: 14px;
    text-align: left;
    color: #868e96;
    margin-top: 6px;
}
.btn_more_img{
    width: 24px;
    height: 24px;
}
.btn_episode_add{
    border-radius: 5px;
    background-color: white;
    border: solid 1px #ff5656;
    color: #ff5656;
    font-size: 14px;
    padding: 12px 16px;
    margin-top: 12px;
}
.info_views{
    width: 12px;
    height: 12px;
    margin-right: 4px;
}
.info_star{
    width: 12px;
    height: 12px;
    margin-right: 4px;
}
.item_rate{
    margin-top: 24px;
}
.item_info{
    padding-left: 36px;
    padding-right: 0px;
}
.btn_more{
    text-align: right;
    padding: 0px;
    cursor: pointer;
}
.work_list_item{
    width: 1000px;
    padding: 24px 0px 24px 0px;
    border-bottom: 1px solid #dee2e6;

}
/*드롭다운 */
button{
    border-style: none;
    background-color: white;
}
.dropbtn{
    background: none;
    border: none;
}
.btn_more{
    text-align: right;
    cursor: pointer;
}
.btn_more_img{
    width: 24px;
    height: 24px;
}
.dropdown-menu{
    top: auto;
    padding: 8px;
    border: 0px;
    border-radius: 5px;
    box-shadow: 0 3px 6px 0 rgba(33, 37, 41, 0.24);
}
.dropbtnList{
    padding: 8px 0px;
}
.info_starArea{
    margin-left: 8px;
}
/* 데이터가 없을 때 */
.emptyArea{
    text-align: center;
    padding-top: 240px;
}
.emptyText{
    font-size: 18px;
	line-height: 1.56;
	letter-spacing: 0.6px;
	text-align: center;
	color: #adb5bd;
    margin: 16px 0px 32px 0px;
}
.btnRedshadow{
    padding: 16px 60px;
    border-radius: 33.5px;
	background-color: #ff5656;
	box-shadow: 0 3px 6px 0 rgba(255, 86, 86, 0.63);
    color: white;
    font-size: 16px;
    font-weight: lighter;
}


</style>



