<template>
    <div id="episodeScript">
        <!-- Transition, css 참고-->
        <!-- https://kr.vuejs.org/v2/guide/transitions.html -->

        <div class="container" ref="body">
            <transition name="fade">
                <div class="cardToolbar" ref="toolbar" v-show="isShow.toolbar">
                    <!-- Button trigger modal -->
                    <button class="toolbarIc" type="button" data-toggle="modal" data-target="#myModal" @click="onClickcharacter">
                        <img src="../assets/ic-person-add-border.svg">
                    </button>
                    <button class="toolbarIc" type="button" data-toggle="modal" data-target="#photoListModal" @click="onClickAddGalleryButton">
                        <img src="../assets/ic_gallery.svg" >
                    </button>
                    <button class="toolbarIc">
                        <img src="../assets/ic-textsms-border.svg" @click="onClickAddloadingButton">
                    </button>
                    <button class="toolbarIcLast">
                        <img src="../assets/ic-time.svg" class="toolbarIcLast" @click="onClickAddTime">
                    </button>
                </div>
            </transition>
            <div class="card col-md-10">
                <div>
                    <div class="cardWrap cardHead">
                        <div class="letterNum">
                            <div class="charactersInfo">{{ test }}</div>
                        </div>
                        <div v-if="script.loading == true" class="more">
                            <img src="../assets/ic-more-horiz.svg" class="btn_more">
                        </div>
                        <div class="time" v-if="script.time.length > 0">
                                <img src="../assets/ic-watch-later-tab.svg" class="ic_watch" >
                                <span>
                                    <input v-model="script.time" class="timeNum formControl" type="time" >
                                </span>
                        </div>
                        <div v-else class="timeHide">
                                <img src="../assets/ic-watch-later-tab.svg" class="ic_watch" >
                                <span>
                                    <input v-model="script.time" class="timeNum formControl" type="time" >
                                </span>
                        </div>
                    </div>
                </div>
                <div class="">
                    <!-- 1. exsist char and char image  -->
                    <div
                        v-if="script.character && script.character.THUMBNAIL_URL > 0"
                        class="profileImage"
                        :style="{ 'background-image': `url(${script.character.THUMBNAIL_URL})` }">
                    </div>
                    <!-- 2. exsist char only (no char image)  -->
                    <div class="profileImageNoneArea" v-else-if="script.character && script.character.THUMBNAIL_URL.length <= 0 && script.character.NAME.length > 0">
                        <button @click="onClickRemoveButtonScripCharacter" class="icClearBtnGrArea">
                            <img src="../assets/ic-cancel-gr.svg" class="icClearBtnGr">
                        </button>
                        <img src="../assets/none.svg" class="profileNoneImg"/>
                    </div>
                    <!-- show char name -->
                    <div class="cardTextWithProfile" v-if="script.character && script.character.NAME.length > 0 && script.image.length == 0">
                        <div v-if="script.character && script.character.NAME" class="profileText">
                            {{script.character.NAME}}
                        </div>
                        <div class="cardText">
                            <textarea v-model="script.content" class="cardText yourtextarea" maxlength="140" name="workTitle" type="text" required placeholder="내용을 입력해 주세요" @input="changeCharactersInfo">
                            </textarea>
                        </div>
                    </div>
                    <div class="cardTextWithProfileWithGallery" v-else-if="script.character && script.character.NAME.length > 0 && script.image.length > 0">
                        <div v-if="script.character && script.character.NAME" class="profileText">
                            {{script.character.NAME}}
                        </div>
                        <div class="cardText">
                            <textarea v-model="script.content" class="cardText yourtextarea" maxlength="140" name="workTitle" type="text" required placeholder="내용을 입력해 주세요" @input="changeCharactersInfo">
                            </textarea>
                        </div>
                    </div>
                    <div class="cardTextWithGallery" v-else-if="script.image.length > 0 && script.character == undefined">
                        <div class="cardText">
                            <textarea v-model="script.content" class="cardText yourtextarea" maxlength="140" name="workTitle" type="text" required placeholder="내용을 입력해 주세요" @input="changeCharactersInfo">
                            </textarea>
                        </div>
                    </div>
                    <div class="cardText" v-else>
                        <textarea v-model="script.content" class="cardText yourtextarea" maxlength="140" name="workTitle" type="text" required placeholder="내용을 입력해 주세요" @input="changeCharactersInfo">
                        </textarea>
                    </div>
                </div>
                <div class="cardBgArea">
                    <div class="bgImage"  :style="{ 'background-image': `url(${script.image})` }" v-if="script.image.length > 0 ">
                        <button @click="onClickRemoveButtonScriptGallery" class="icClearBtnGrAreaforGallery">
                            <img src="../assets/ic-cancel-gr.svg" class="icClearBtnGr">
                        </button>
                    </div>
                </div>
            </div>
            <transition name="fade">
                <div class="col-md-1 option-buttons" ref="buttons" v-show="isShow.buttons">
                    <button class="ic_add_circle" @click="onClickAddButton">
                        <img src="../assets/ic-add-wh.svg" class="ic_add_wh"/>
                    </button>
                    <button class="ic_add_delete" @click="onClickRemoveButton">
                        <img src="../assets/ic-delete.svg" class="ic_delete"/>
                    </button>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>

export default {
    name: 'episodeScript',
    props: {
        script: {
            type: Object,//옵셔널. 스크립트를 받을건데 이게 넘어온 값을 오브젝트 타입으로 받겠다. 만약 오브젝트가 아니면 에러남.
            required: true //이 값은 필수. 만약 sciprt데이터를 못 넘겨받으면 에러남.
        },

    },
    data() {
        return {
            test: '0/140',
            isShow: {
                toolbar: false,
                buttons: false
            }
        };
    },
    methods:{
        onClickRemoveButtonScriptGallery(){
            this.$emit('onClickRemoveButtonScriptGallery');
        },
        onClickRemoveButtonScripCharacter(){
            this.$emit('onClickRemoveButtonScripCharacter');
        },
        onClickAddGalleryButton(){
            this.$emit('onClickAddGalleryButton');
        },
        onClickcharacter(){
            this.$emit('onClickcharacter');
        },
        onClickAddTime(){
            /*this.time = "1"
            var timeInput = document.getElementById("timeInput");
            if(timeInput.style.display === "none"){
                timeInput.style.display = "block";
            }
            else {
                timeInput.style.display = "none";
                this.time = ""
            }*/
            this.$emit('onClickAddTime');
        },
        onClickAddloadingButton(){
            this.$emit('onClickAddloadingButton');
        },
        onClickAddButton() {
            this.$emit('onClickAddButton');
        },
        onClickRemoveButton() {
            this.$emit('onClickRemoveButton');
        },
        changeCharactersInfo() {
            // console.log(event.target.value);
            // event = input 에 값이 입력되서 생성된 이벤트
            // event.target
            // target = input
            // target.value = input에 담긴값
            // this.test = event.target.value.length+"/140"

            // event.target.value == this.script.content
            this.test = this.script.content.length+"/140"
            // 해결!
        },
        showElement(target) {
            this.isShow[target] = true;
        },
        hideElement(target) {
            this.isShow[target] = false;
        }
    },
    mounted () {
        $(this.$el).find('.container').hover(() => {
            this.showElement('buttons');
        }, () => {
            this.hideElement('buttons');
        });

        $(this.$el).on('click', () => {
            this.showElement('toolbar');
        });
        $(document).on('click', e => {
            /**
             * @func HTMLElement.contains
             * @desc contains메소드는 인자로 받은 엘리먼트가 호출한 엘리먼트에 포함되어 있는 엘리먼트인지의 여부를 반환한다
             */
             if( this.$refs.body.contains != undefined ){
                // console.log(this.$refs.body);
                if (!this.$refs.body.contains(e.target)) {
                    this.hideElement('toolbar');
                }
             }

        });
    }
};
</script>

<!-- npm으로 node-sass, sass-loader -->
<!-- npm i -D node-sass sass-loader -->

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.icClearBtnGr{
    vertical-align: baseline;
}
.icClearBtnGrArea{
    position: absolute;
    left: 50px;
}
.icClearBtnGrAreaforGallery{
    left: 50px;
    margin: 4px;
    background-color: transparent;
}
.profileImageEpmty{
    display: none;
}
.profileImageNoneArea{
    width: 50px;
    height: 50px;
    padding: 0px;
    margin-right: 16px;
    float: left;
}
.profileNoneImg{
    width: 50px;
    height: 50px;
}
button{
    border-style: none;
    padding: 0px;
}
textarea{
    resize: none;
    border: 0;
}
.container{
    width: 100%;
    padding: 0px;
}
.row{
    margin: 0px;
}
.profileImage{
    width: 50px;
    height: 50px;
    background-color: black;
    border: solid 1px #dee2e6;
    border-radius: 50%;
    padding: 0px;
    margin-right: 16px;
    float: left;
}

.bgImage{
    width: 100px;
    height: 100px;
    background-color: black;
    border: solid 1px #dee2e6;
    border-radius: 5px;
}
.profileText{
    font-size: 16px;
    color: #5c7cfa;
    padding: 0px;
}
.profileTextNone{
    display: none;
}
.letterNum{
    color: #868e96;
    padding: 0px;
    float: left;
}
.time{
    text-align: center;
    margin: 0 auto;
}
.timeNum{
    font-size: 12px;
    color: #868e96;
    padding: 0px;
    text-align: center;
}
.ic_watch{
    width: 14px;
    height: 14px;
}
.card{
    margin-bottom: 24px;
    border-radius: 5px;
    background-color: #ffffff;
    box-shadow: 0 3px 6px 0 rgba(33, 37, 41, 0.24);
    border: solid 1px #dee2e6;
    padding: 16px;
}
.cardHead{
    margin-bottom: 16px;
    padding: 0px;
    text-align: center;
}
.cardText{
    min-height: 80px;
    font-size: 16px;
    line-height: 1.62;
    text-align: left;
    color: #495057;
    padding: 0px;

}
.cardTextWithProfile{
    float: left;
    width: 90%;
}
.cardTextWithProfileWithGallery{
    float: left;
    width: 75%;
}
.cardTextWithGallery{
    float: left;
    width: 84%;
}

.more{
    padding: 0px;
    text-align: right;
    float: right;
}
.cardBgArea{
    padding: 0px;
    text-align: -webkit-right;
    float: right;
}
.ic_add_circle{
    border-radius: 50%;
    width: 32px;
    height: 32px;
    background-color: #343a40;
    text-align: center;
    margin-left: 8px;
}
.ic_add_wh{
    vertical-align: -webkit-baseline-middle;
}
.ic_add_delete{
    border-radius: 50%;
    width: 32px;
    height: 32px;
    background-color: #dee2e6;
    text-align: center;
    margin-left: 8px;
    margin-top: 8px;
}
.ic_delete{
    vertical-align: -webkit-baseline-middle;
    width: 16px;
    height:16px;
}

.sideFunction{
    padding: 16px;
    width: 65px;
    border-radius: 5px;
    background-color: #ffffff;
    border: solid 1px #ced4da;
    float: right;
}
.functionIcWrap {
    text-align: center;
}
.functionIc {
    margin-top: 8px;
}
.col-md-1 {
    padding: 0px;
}

.cardToolbar{
    display: inline-block;
    border: solid 1px #ced4da;
    background-color: #ffffff;
    border-radius: 5px;
    padding: 8px 16px 8px 16px;
    box-sizing: border-box;
    box-shadow: 0 3px 6px 0 rgba(33, 37, 41, 0.24);
    margin-bottom: 8px;
}
.toolbarIc{
    margin-right: 16px;
}
.toolbarIcLast{
    margin-right: 0px;
}
.bgArea{
    margin-bottom: 24px;
}
.timeHide{
    visibility: hidden;
}
textarea{
    width: 100%;
    -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   box-sizing: border-box;
}
/* transition */
/* transition이 작동하는 동안 바인딩 되는 클래스들 */
/* 기본적으로 transition_name-enter-active, transition_name-leave-active로 구성된다*/
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
/* transition이 시작될 때, 끝날 때 추가되는 클래스 */
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}

</style>



