import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import WorkDetail from '@/components/WorkDetail';
import WorkUpload from '@/components/WorkUpload';
import WorkModify from '@/components/WorkModify';
import WorkEpisode from '@/components/WorkEpisode';
import SubHeader from '@/components/SubHeader';
import SideList from '@/components/SideList';
import FanficEditor from '@/components/FanficEditor';



Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/work/upload',
      name: 'workUpload',
      component: WorkUpload,
    },
    {
      path: '/work/episode/:fanficIdx/:title',
      name: 'WorkEpisode',
      component: WorkEpisode,
    },
    {
      path: '/work/subHeader',
      name: 'SubHeader',
      component: SubHeader,
    },
    {
      path: '/work/sideList',
      name: 'SideList',
      component: SideList,
    },
    {
      path: '/work/FanficEditor',
      name: 'FanficEditor',
      component: FanficEditor,
    },
    {
      path: '/work/detail/:id',
      name: 'workDetail',
      component: WorkDetail,
    },
    {
      path: '/work/modify/:id', //index.js에서 id란 값을 받겠다고 지정
      name: 'WorkModify',
      component: WorkModify,
    },
  ],
});



