<template>
    <div id="header" role="navigation">
        <div class="header_wrap">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                  <router-link to="/" class="headTitle">
                          팬픽
                  </router-link>
              </li>
              <li class="breadcruamb-item active">{{ breadcrumbs }}</li>
            </ol>

            <ul class="nav_right">
                <li>
                    <router-link to="/work/upload">
                        <button class="work_upload">
                            <span>작품 등록하기</span>
                        </button>
                    </router-link>
                </li>
                <div class="dropdown user_info" v-on:click="showDropDown">
                    <button class="dropbtn">
                        <div id="profileDropDown" class="dropdown-content">
                            <a href="#" class="nameInfo">아메리카노<span>appphotocard@gmail.com</span></a>
                            <a href="#" class="logout">로그아웃</a>
                        </div>
                    </button>
                </div>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    props: ['breadcrumbs'],//<Header2 breadcrumbs="작품 정보"> 작품정보라는 값을 받아옴
    name: 'header',
    methods: {
        showDropDown: function (event) {
            console.log('show');
            document.getElementById("profileDropDown").classList.toggle("show");
        },
    },
    data() {
        return {
            msg: 'Welcome to Your Vue.js App',
        };
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #header{
        position: relative;
        height: 100px;
        width: 1000px;
        background-color: #fff;
        padding: 24px 0px 24px 0px;
        margin-bottom: 16px;
        margin: auto;
    }
    .nav-title{
        font-size: 32px;
        font-weight: bold;
        text-align: left;
        color: rgba(0, 0, 0, 0.87);
        float: left;
        margin: 0px;
    }
    .work_upload{
        border-radius: 16.5px;
        background-color: #ff5656;
        color: #fff;
        padding: 8px 16px;
        display: inline-block;
        font-size: 14px;
        font-weight: lighter;
    }
    li{
        list-style: none;
    }
    button{
        border-style: none;
    }
    .nav_right{
        float: right;
        display: flex;
        margin: 0px;
            }
    .user_info{
        width: 32px;
        height: 32px;
        background-color: black;
        border-radius: 50px;
        margin-left: 16px;
    }
    .dropdown {
        position: relative;
        display: inline-block;
    }

    .dropbtn{
        background: none;
        border: none;
    }

    /* Dropdown Content (Hidden by Default) */
    .dropdown-content {
        display: none;
        position: absolute;
        background-color: #fff;
        min-width: 140px;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 1;
        right: 0;
        border-radius: 5px;
        margin-top: 24px;
    }

    /* Links inside the dropdown */
    .dropdown-content a {
        padding: 16px;
        text-decoration: none;
        text-align: left;
        display: block;
    }
    /* Change color of dropdown links on hover */
    .dropdown-content a:hover {background-color: #f1f1f1}

    /* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
    .show {display:block;}

    .nameInfo{
        color: #343a40;
    }
    .logout{
        color: #868e96;
    }
    .breadcrumb{
        float: left;
        background-color:transparent;
        padding: 0px;
    }
    .headTitle{
        font-size: 32px;
        font-weight: bold;
        color: #343a40;
    }
    .breadcrumb>.active{
        font-size: 32px;
        color: #343a40;
    }

</style>



